<template>
	<view class="container">
		<view class="intro">welcome</view>
		<view>
			<button @click="openHome">打开首页</button>
			<button @click="openVideo">打开视频</button>
			<button @click="testConn">测试连接</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				href: 'https://uniapp.dcloud.io/component/README?id=uniui'
			}
		},
		methods: {
			openHome() {
				uni.navigateTo({
					url: '/pages/home/home'
				})
			},
			openVideo() {
				uni.navigateTo({
					url: '/pages/video/video'
				})
			},
			testConn() {
				this.$axiosClient.sendInfo("/api/public/hss/fsi/invoke", {}, {
						successCallback: (data) => console.log(data)
						},{
							failCallback: (result) => console.log(result)
							},
						)
					}
				}
			}
</script>

<style>
	.container {
		padding: 20px;
		font-size: 14px;
		line-height: 24px;
	}
</style>
